@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gentium+Book+Basic:ital@1&family=Zen+Kaku+Gothic+Antique:wght@300;400&display=swap);
.nav-bar {
    position: relative;
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
}

ul h3 {
    font-weight: 00;
    font-size: 4rem;
    color: rgba(18, 18, 18, 1);
    border-bottom: 1px solid rgba(18, 18, 18, 0.438);
}

.nav-links {
    display: flex;
    flex-flow: row;
    height: 10vh;
    font-size: 4rem;
    justify-content: space-evenly;
    align-items: center;
    margin: 0.3em auto 0.5em auto;
    position: -webkit-sticky;
    position: sticky;
}

.nav-links li {
    display: block;
    text-decoration: none;
    color: rgba(18, 18, 18, 1);
    border-bottom: 1px solid rgba(18, 18, 18, 0.438);
}

.link-line {
    text-decoration: none;
}

.cart-count-container {
    height: 60px;
    width: 60px;
    display: flex;
    align-items: center;
    color: black;
    position: absolute;
    right: 0;
    margin-right: 1em;
}

.cart-count-wrapper {
    height: 60px;
    width: 60px;
    position: relative;
}


.cart {
    color: rgb(117, 117, 117);
    position: absolute;
}

.cart:hover {
    cursor: pointer;
}

.cart-count {
    height: 1.6rem;
    width: 1.6rem;
    font-size: 1.2rem;
    margin: auto;
    position: absolute;
    right: 0;
    text-align: center;
    border-bottom: none;
    text-shadow: none;
    color: #FFF;
    background-color: rgb(58, 173, 202);
    border-radius: 50%;
    pointer-events: none;
}


.home-background-wrapper {
    background-repeat: no-repeat;
    background-color: #FBFAF5;
}

.hero-container {
    position: relative;
}

.hero-image {
    -webkit-filter: brightness(75%);
            filter: brightness(75%);
    width: 100%;
    height: auto;
}

.title-container {
    display: flex;
    flex-flow: column;
    position: absolute;
    top: 0;
    right: 0;
    margin: 4em 4em 0 0;
    text-align: center;
}

.title {
    font-size: 5.4rem;
    border-bottom:  1px solid #FBFAF5;
    text-shadow: 2px 2px black;
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    font-weight: 400;
    color: #FBFAF5;
    margin-bottom: 0.2em;
}
.title-container a {
    text-align: left;
}

.hero-button {
    margin-top: 0;
    font-size: 2.6rem;
    text-align: center;
    width: 20%;
    height: auto;
    padding-bottom: 0.2em;
    color: #FBFAF5;
    background-color: #649f60;
    font-weight: 400;
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    border: none;
    cursor: pointer;
}

.title-container button:hover {
    background-color: #50804d;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
   
}

.home-span {
    font-size: 2rem;
    margin-top: 1em;
    text-align: center;
    font-family: 'Gentium Book Basic', serif;
}

.home-grid-container {
    display: flex;
    flex-flow: row;
    margin: auto;
    width: 100%;
    justify-content: center;
    grid-gap: 20em;
    margin-top: 3em;
    margin-right: 2em;
}

.home-grid-container a {
    color: rgba(18,18,18,1);
    text-decoration: none;
}



.home-grid-container img {
    width: 31.25em;
    height: 31.25em;
    object-fit: cover;
}

.home-card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    background-color: white;
    margin-bottom: 6em;
    border: 1px solid rgba(18,18,18,0.76);
}

.home-card:hover {
    box-shadow: 0 16px 16px 0 rgba(0,0,0,0.3);
    cursor: pointer;
}

.home-item-name {
    font-size: 2rem;
    margin: 0.2em auto 0.5em auto;
    font-family: 'Merriweather', serif;
    text-align: center;

}
.shop-container {
    display: grid;
    grid-template-columns: repeat(5, 300px);
    grid-template-rows: repeat(2);
    grid-gap: 2em;
    justify-content: center;
    margin: 5em 0 2em 0;
}

.shop-item {
    width: 300px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    outline: 1px solid rgba(0,0,0,0.2);
    transition: 0.3s;
}

.shop-item:hover {
    box-shadow: 0 16px 16px 0 rgba(0,0,0,0.3);
    transition: 0.3s;
}

.shop-item-img {
    width: 300px;
    height: 300px;
    border-bottom: 1px solid rgba(18, 18, 18, 0.438);
}

.shop-item-name {
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    font-weight: 600;
    text-align: center;
    font-size: 1.6rem;
    margin-top: 0.5em;
    color: rgba(18, 18, 18, 1);
}

.shop-item-price {
    position: absolute;
    height: auto;
    bottom: 0;
    margin-bottom: 1em;
    color: rgba(18, 18, 18, 0.76);
}

.shop-item-container {
    position: relative;
    height: 8em;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
}
.item-container {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    margin-top: 5em;
    grid-gap: 10em;
    gap: 10em;
}

.item-name {    
    text-align: center;
    font-size: 4.6rem;
    font-family: 'Merriweather', serif;
    color: black;
    margin-bottom: 0;
}

.item-price {
    margin: 1em 0 1em 0;
    font-size: 1.6rem;
}

.item-img {
    height: 500px;
    width: 500px;
}

.item-info {
    display: flex;
    flex-flow: column;
    text-align: center;
    width: 30em;
}

.item-quantity-container {
   display: flex;
   justify-content: center;
   grid-gap: 0.2em;
   gap: 0.2em;
   margin-bottom: 2em;
}

.item-quantity {
    width: 3em;
    text-align: center;
}

.item-quantity-adjustment {
    border: 1px solid rgba(0, 0, 0, 0.308);
    cursor: pointer;
}

.item-quantity-adjustment:hover {
    background-color: rgba(0, 0, 0, 0.144);
}

.add-to-cart {
    font-size: 2rem;
    padding: 0.5em;
    background-color: rgba(18, 18, 18, 1);
    color: #FBFAF5;
    border: none;
    border-radius: 50px;
    cursor: pointer;
}

.add-to-cart:hover {
    background-color: rgb(37, 37, 37);
    -webkit-transform: scale(1.02, 1.02);
            transform: scale(1.02, 1.02);
}

.item-description {
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    font-weight: 400;
    color: black;
    width: 80%;
    margin: 4em auto 5em auto;
    font-size: 1.4rem;
    text-align: left;
}

.item-description li {
    list-style: none;
    margin-bottom: 0.4em;
}

.item-description li::before {
    content: "• ";
    color: rgb(0, 0, 0);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


.footer-notes {
    position: absolute;
    width: 100%;
    bottom: 0;
    display: flex;
    flex-flow: row;
    height: 3.6em;
    background-color: rgba(18, 18, 18, 1);
    align-items: center;
    padding-left: 2em;
    color: grey;
    grid-gap: 0.5em;
    gap: 0.5em;
}

.footer-note {
    height: auto;
    font-size: 1rem;
    color: grey;
    margin: 0;
}

.copyright {
    margin-left: auto;
    margin-right: 1em;
}

.gap {
    margin-right: 1em;
}
.cart-sidebar {
    height: 100%;
    width: 20%;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 60px;
    color: #FBFAF5;
}

.cart-item-container {
    display: flex;
    flex-flow: row;
    margin: auto auto 2em auto;
    align-items: center;
}

.cart-item-info {
    display: flex;
    flex-flow: column;
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    font-size: 1rem;
    margin-left: 2em;
    color: #FBFAF5;
    grid-gap: 0.2em;
    gap: 0.2em;
}



.cart-img {
    margin-left: 2em;
    width: 100px;
    height: 100px;
}

.qty-handle {
    display: flex;
    margin-bottom: 1em;
}

.qty-handle span {
    margin-right: 1em;
}

.qty-adjustment {
    cursor: pointer;
    border: 1px solid #FBFAF5;
}

.qty-adjustment:hover {
    background-color: rgb(56, 56, 56);
}

.empty-cart {
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    font-size: 1.2rem;
    width: 100%;
    margin: 2em 0 0 2em;
    text-align: center;
}

.checkout {
    font-family: 'Zen Kaku Gothic Antique', sans-serif;
    display: flex;
    flex-flow: column;
    grid-gap: 1em;
    gap: 1em;
}

.total-price {
    margin-left: 2em;
    color: #FBFAF5;
}

.submit-order {
    font-size: 1rem;
    width: 50%;
    margin-left: 2em;
    text-align: center;
    border: none;
    padding: 1em;
    border-radius: 50px;
}

.submit-order:hover {
    cursor: pointer;
    background-color: rgb(34, 34, 34);
    color: #FBFAF5;
    transition:  0.3s;
    outline: 1px solid #fbfaf581;
}

.close {
    margin: 0.3em 0.3em auto auto;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

.close:hover {
    color: #fbfaf5b6;
}

.delete-item {
    width: 6em;
    background-color: #111;
    color: #FBFAF5;
    border: none;
    outline: 1px solid #FBFAF5;
    cursor: pointer;
}

.delete-item:hover {
    background-color: rgb(56, 56, 56);
}

body {
    margin: 0;
}

html {
    font-family: sans-serif;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
    /* outline: 1px solid red; */
}

.App {
    background-color: #FBFAF5;
    background-repeat: no-repeat;
    background-size: contain;
    min-height: 100%;
    width: 100%;
    height: auto;
    position: absolute;
}



